import React, { useEffect, useState, useRef } from 'react'
import debounce from "lodash/debounce";


import cl from '../chatPages.module.scss';

import { w3cwebsocket as W3CWebSocket } from "websocket";
import MessageForm from '../components/MessageForm/MessageForm';
import MessagesWrapper from '../components/MessagesWrapper/MessagesWrapper';

import { useSelector } from 'react-redux';

const ConnectedOrNotMessage = ({ connected }) => {

    console.log(connected)

    if (connected === false) {
        return (
            <div className={cl.notConncetedMessageWrapper}>
                <div className={cl.notConncetedMessageContent}>
                    <span className={cl.notConncetedMessageText}>
                        Conncetion failed. Retrying
                    </span>
                    <span className={cl.notConncetedMessageSpinnerWrapper}>
                        <span className={cl.notConncetedMessageSpinner} />
                    </span>
                </div>
            </div>
        )
    } else if (connected) {
        return (
            <span className={cl.conncetedMessage} />
        )
    } else {
        return (
            <></>
        )
    }

}



const TopHeader = (props) => {
    const { roomname, connected, memberThatIsTyping } = props

    return (
        <div className={cl.top_wrapper}>

            <div className={cl.top_wrapper_content}>
                Room: {roomname} ..

                {memberThatIsTyping ?
                    <>
                        {memberThatIsTyping} is Typing
                    </>
                    :
                    <>
                        2 members
                    </>
                }

            </div>

            <ConnectedOrNotMessage connected={connected} />
        </div>
    )
}




function PersonalChatPage() {

    const [message, setMessage] = useState('hello world')

    // 
    const [messages, setMessages] = useState({
        "Tuesday": [
            { "type": "text", "username": "admin", "message": "hi" },
            { "type": "text", "username": "some_username", "message": "admin" },
            { "type": "text", "username": "alan", "message": "hi" },
            { "type": "text", "username": "some_username", "message": "alan" },
            { "type": "text", "username": "hi", "message": "hi" },
            { "type": "text", "username": "some_username", "message": "hi" },
            { "type": "text", "username": "hi", "message": "hi" },
            { "type": "text", "username": "some_username", "message": "hi" },
        ],
        "Yesterday": [
            { "type": "text", "username": "hi", "message": "hi" },
            { "type": "photo", "username": "some_username", "message": "hi" },
            { "type": "text", "username": "hi", "message": "hi" },
            { "type": "file", "username": "some_username", "message": "hi" },
            { "type": "text", "username": "hi", "message": "hi" },
            { "type": "text", "username": "some_username", "message": "hi" },
            { "type": "text", "username": "hi", "message": "hi" },
            { "type": "text", "username": "some_username", "message": "hi\n  So, What is up?\n hello world\n" },
        ],
        "Today": [
            { "type": "text", "username": "some_username", "message": "hi" },
            { "type": "text", "username": "alan", "message": "q" },
            { "type": "text", "username": "some_username", "message": "hi" },
            { "type": "text", "username": "some_username", "message": "hello" },
            { "type": "text", "username": "alan", "message": "hi" },
            { "type": "text", "username": "some_username", "message": "hi" },
        ]
    })

    const [isUserTyping, setIsUserTyping] = useState(false)

    const [memberThatIsTyping, setMemberThatIsTyping] = useState(null)

    const handleIsMemberTyping = debounce(function () {
        // continually delays setting "isTyping" to false for {number}ms until the member has stopped typing and the delay runs out
        setMemberThatIsTyping(null);
    }, 1500);

    const scrollDown = () => {
        if (messages_wrapper_ref.current) {
            messages_wrapper_ref.current.scrollTop = messages_wrapper_ref.current.scrollHeight
        }
    }

    const messages_wrapper_ref = useRef()

    const chatSocketRef = useRef(null)

    const [connected, setConnected] = useState(false) // null

    const TOKEN = useSelector(state => state.auth.token)

    const roomname = "hello"

    const USERNAME = useSelector(state => state.auth?.user?.username)

    console.log("USERNAME", USERNAME)


    const onSubmit = () => {

        if (chatSocketRef.current) {
            console.log("send")

            chatSocketRef.current.send(JSON.stringify({
                command: 'send_message',
                data: {
                    username: USERNAME,
                    message: message,
                }
            }))

        }

        // TODO

        // ! TO edit
        let new_message = { "type": "text", "username": USERNAME, "message": message.trim() }

        let m = Object.assign({}, messages)

        let o = m["Today"] ? m["Today"] : []

        m['Today'] = [
            ...o,
            new_message
        ] // 

        new Promise((resolve) => {
            setMessages(m)
            resolve(1)
        }).then(() => {
            scrollDown()
        })

        // setMessage('') // TODO
    }


    const OpenWebsocketConnection = () => {

        chatSocketRef.current = new W3CWebSocket(
            `ws://${"192.168.0.106:8000"}/ws/chat/${roomname}/`
        )

        const chatSocket = chatSocketRef.current

        chatSocket.onopen = function () {
            console.log("opened")
            setConnected(true)
        }


        chatSocket.onmessage = (event) => {

            const data = JSON.parse(event["data"])

            switch (data["action"]) {
                case "user_is_typing":
                    console.log(data["username"], USERNAME)
                    if (data["username"] !== USERNAME) {
                        setMemberThatIsTyping(data["username"])
                        handleIsMemberTyping()
                    }
                    break;
                default:
                    console.log(data)
                    break;
            }

            // const dataFromServer = JSON.parse(message.data)
            // if (dataFromServer) {
            //     console.log(dataFromServer)

            //     const updatedMessages = [...messages, dataFromServer]
            //     setMessages(updatedMessages)

            //     if (USERNAME === dataFromServer.username) {
            //         scrollDown()
            //     }

            // }
        }

        chatSocket.onerror = (e) => {
            console.log(e)
        }

        chatSocket.onclose = () => {
            console.log("closed connection")
            setConnected(false)
        }

    }


    useEffect(() => {
        document.getElementsByTagName("body")[0].style.overflow = 'hidden'
        scrollDown() // ?

        OpenWebsocketConnection()


        return () => {
            document.getElementsByTagName("body")[0].style.overflow = "auto";

            if (chatSocketRef.current) {
                chatSocketRef.current.close()

                chatSocketRef.current = null
            }
        }
    }, [])


    useEffect(() => {

        let timeout = null

        if (!connected) {
            let y = 0
            timeout = setInterval(() => {
                if (connected) {
                    clearInterval(timeout)
                } else {
                    console.log(++y, "trying")
                    if (chatSocketRef.current) {
                        OpenWebsocketConnection()
                    }
                }
            }, 2000)
        }

        return () => clearInterval(timeout)

    }, [connected])


    useEffect(() => {

        if (isUserTyping) {
            // TODO

            if (chatSocketRef.current) {

                try {
                    chatSocketRef.current.send(JSON.stringify({
                        action: 'user_is_typing',
                        data: {},
                    }))
                } catch (e) {
                    console.log(e)
                }

            }

        }

    }, [isUserTyping])



    return (
        <div className={cl.main_wrapper}>
            <div className={cl.main_content_wrapper}>
                <div className={cl.main_content}>
                    <div className={cl.main}>

                        <TopHeader
                            roomname={roomname}
                            connected={connected}
                            memberThatIsTyping={memberThatIsTyping}
                        />

                        <MessagesWrapper
                            messages={messages}
                            username={USERNAME}
                            scrollDown={scrollDown}
                            messages_wrapper_ref={messages_wrapper_ref}
                        />

                        <div className={cl.messageFormWrapper}>
                            <MessageForm
                                message={message}
                                setMessage={setMessage}
                                onSubmit={onSubmit}
                                setIsUserTyping={setIsUserTyping}
                            />
                        </div>

                    </div>

                </div>

            </div>
        </div>
    )
}

export default PersonalChatPage;