import React, { useEffect, useState, useContext } from 'react';
import { useTranslation } from 'react-i18next';
import cookies from 'js-cookie';


import cl from "./Navbar.module.scss";

// import "materialize-css/dist/css/materialize.min.css";
import "./materialize.css"


import SidebarMenu from '../SidebarMenu/SidebarMenu';

import { ThemeContext } from '../../context/themes';
import { themes } from '../../context/themes';
import { languages } from '../../languages/languages';


import { NavLink, Link } from 'react-router-dom';

import { useSelector } from 'react-redux';


import ThemeSvg from './svg-icons/ThemeSvg';
import LangSvg from './svg-icons/LangSvg';


const Logo = function () {
    return (
        <div className={cl.logo_wrapper}>
            <Link to="/">
                LOGO
            </Link>
        </div>
    )
}


const SelectLang = function ({ chLang, currentLanguageCode, languages }) {
    return (
        <label className={`${cl.label} nav_icon`}>
            <span className={cl.svg}>
                <span className={cl.svg_wrapper}>
                    <LangSvg />
                </span>
            </span>
            <select defaultValue={currentLanguageCode} onChange={chLang} className={`${cl.select}`}>
                {languages.map(({ code, name, country_code }) =>
                    <option key={country_code} value={code} className={cl.option}>
                        &nbsp;{name}&nbsp;
                    </option>
                )}
            </select>
        </label>
    )
}

const SelectTheme = ({ chTheme, currentTheme, themes }) =>
    <label className={`${cl.label} nav_icon`}>
        <span className={cl.svg}>
            <span className={cl.svg_wrapper}>
                <ThemeSvg />
            </span>
        </span>
        <select defaultValue={currentTheme} onChange={chTheme} className={cl.select}>
            {themes.map((theme) =>
                <option key={theme} value={theme} className={cl.option}>
                    &nbsp;{theme}&nbsp;
                </option>
            )}
        </select>
    </label>

const Burger = () =>
    <span className={`${cl.burger_wrapper_main} burger`}>
        <span
            data-target="slide-out"
            className={`sidenav-trigger ${cl.burger_wrapper}`}>
            <span className={cl.burger}>
                <svg viewBox="0 0 46 32" width="26" height="20">
                    <path
                        d="M0 0h45.714v4.571h-45.714v-4.571zm0 13.714h45.714v4.571h-45.714v-4.571zm0 13.715h45.714v4.571h-45.714v-4.571z">
                    </path>
                </svg>
            </span>
        </span>
    </span>



function Navbar({ changeTheme }) {

    const isUserAuthenticated = useSelector(state => state.auth.isAuthenticated)

    const currentLanguageCode = cookies.get('i18next') || 'en'
    const currentLanguage = languages.find((l) =>
        l.code === currentLanguageCode
    )
    const { t, i18n } = useTranslation()

    useEffect(() => {
        document.body.dir = currentLanguage.dir || 'ltr'
    }, [currentLanguage, t])

    const chLang = (e) => {
        e.preventDefault()
        i18n.changeLanguage(e.target.value)
    }


    const theme = useContext(ThemeContext);

    const [currentTheme, setCurrentTheme] = useState(theme)

    const chTheme = (e) => {
        let th = e.target.value

        e.preventDefault()
        changeTheme(th)
        setCurrentTheme(th)
    }


    return (
        <React.Fragment>
            <nav className={`${cl.nav_main_wrapper} nav`}>
                <div className={cl.nav_content}>

                    <Logo />

                    <div className={cl.nav_links_wrapper}>

                        {[
                            [
                                `${t('main_content.menu.main', "main")}`,
                                "/"
                            ],
                            [
                                `${t('main_content.menu.about_me', "about me")}`,
                                "about-me"
                            ],
                            [
                                `${t('main_content.menu.contact_me', "contact me")}`,
                                "contact-me"
                            ],
                        ].map(l =>
                            <NavLink
                                key={l[1]}
                                to={l[1]}
                                className={
                                    (navData) => navData.isActive ? `${cl.nav_link} ${cl.active} nav_link active` : `${cl.nav_link} cl.nav_link`
                                }
                            >
                                {l[0]}
                            </NavLink>
                        )}

                    </div>

                    <div className={cl.nav_content_right}>

                        {!isUserAuthenticated &&
                            <div className={`${cl.auth_links}`}>
                                {[
                                    [
                                        `${t('main_content.menu.top_content.login', "login")}`,
                                        "/users/login"
                                    ],
                                    [
                                        `${t('main_content.menu.top_content.register', "register")}`,
                                        "/users/register"
                                    ]
                                ].map(l =>
                                    <NavLink
                                        key={l[1]}
                                        to={l[1]}
                                        className={
                                            (navData) => navData.isActive ? `${cl.auth_link} ${cl.active}` : `${cl.auth_link}`
                                        }>
                                        {l[0]}
                                    </NavLink>
                                )
                                }
                            </div>
                        }

                        <div className={cl.nav_content_right__end}>
                            <SelectLang
                                chLang={chLang}
                                currentLanguageCode={currentLanguageCode}
                                languages={languages}
                            />
                            <SelectTheme
                                chTheme={chTheme}
                                currentTheme={currentTheme}
                                themes={themes}
                                color={theme}
                            />
                            <Burger />
                        </div>

                    </div>

                </div>
            </nav>

            <SidebarMenu
                close_sidenav={() => ""} //sidenav.close()
            />
        </React.Fragment >
    )
}

export default Navbar;