import React, { useEffect, useRef, useState } from 'react'
import Loader from '../../../../components/UI/Loader/Loader';
import useScroll from '../../../../hooks/useScroll';
import TextMessage from '../TextMessage/TextMessage';
import cl from './MessagesWrapper.module.scss';



const GoDownBtn = (props) => {
    const { messages_wrapper_ref, scrollDown } = props

    const [goDownBtnIsShown, setGoDownBtnIsShown] = useState(false)

    const [lastScrollY, setLastScrollY] = useState(0)

    const controlGoDownBtnVisibility = () => {

        if (messages_wrapper_ref.current) {
            const SCROLL_POS_Y__FROM_BOTTOM_WHEN_GO_DOWN_BTN_IS_HIDDEN = 100

            const DIFFERENCE_OR_DELAY_BETWEEN_LAST_AND_CURRENT_SCROLL_Y_POS = 100

            const messages_wrapper_element = messages_wrapper_ref.current

            const scrollPosYFromBottom = (
                Number(messages_wrapper_element.scrollHeight)
                - Number(messages_wrapper_element.scrollTop)
                - Number(messages_wrapper_element.offsetHeight)
            ) //? Why 0.7

            if (Math.abs(scrollPosYFromBottom - Number(lastScrollY)) > DIFFERENCE_OR_DELAY_BETWEEN_LAST_AND_CURRENT_SCROLL_Y_POS) {
                if (scrollPosYFromBottom < SCROLL_POS_Y__FROM_BOTTOM_WHEN_GO_DOWN_BTN_IS_HIDDEN
                    ||
                    scrollPosYFromBottom > lastScrollY) {
                    setGoDownBtnIsShown(false)
                } else {
                    setGoDownBtnIsShown(true)
                }

                setLastScrollY(scrollPosYFromBottom)
            }
        }

    }

    useEffect(() => {
        if (messages_wrapper_ref.current) {
            messages_wrapper_ref.current.addEventListener('scroll', controlGoDownBtnVisibility)
        }

        return () => {

            if (messages_wrapper_ref.current) {
                messages_wrapper_ref.current.removeEventListener('scroll', controlGoDownBtnVisibility)
            }

        }
    }, [lastScrollY])


    return (
        <div
            onClick={scrollDown}
            className={`${cl.scrollDownBtn} ${goDownBtnIsShown && cl.scrollDownBtnVisible}`}
        >
            D
        </div>
    )
}


const MessagesWrapper = (props) => {

    const { messages, username, messages_wrapper_ref, scrollDown } = props

    const lastChild = useRef()

    const intersected = useScroll(messages_wrapper_ref, lastChild,
        () => {
            // TODO
            if (true) { // ! if it is not loading
                console.log("Intersected. Need to load new messages!")
            }
        }
    )

    const MessagesAreBeingLoaded = true

    return (

        <div
            className={cl.messages_wrapper}
            ref={messages_wrapper_ref}
        >

            <div className={cl.messages_content}>

                <div>
                    <span ref={lastChild} className={cl.lastChild} />
                    {MessagesAreBeingLoaded && <Loader />}
                </div>

                {Object.keys(messages).length ?

                    Object.keys(messages).map((date, index) =>

                        <div
                            key={index}
                            className={cl.messages_date_group}
                        >
                            <div className={cl.sticky_date_wrapper}>
                                <div className={cl.sticky_date}>
                                    {date}
                                </div>
                            </div>

                            {messages[date].map((data, index) => {
                                return (() => {
                                    switch (data["type"]) {
                                        case "text":
                                            return (
                                                <TextMessage
                                                    key={index}
                                                    data={data}
                                                    isOutgoing={data.username === username}
                                                />
                                            )
                                        case "photo":
                                            return (
                                                <div>
                                                    PHOTO
                                                </div>
                                            )
                                        case "file":
                                            return (
                                                <div>
                                                    FILE
                                                </div>
                                            )
                                        default:
                                            return (
                                                <div>
                                                    ERROR
                                                </div>
                                            )
                                    }
                                })()
                            }
                            )}

                        </div>
                    )

                    :
                    <div className={cl.no_messages_yet_text_wrapper}>
                        <span className={cl.no_messages_yet_text}>
                            No messages yet.
                        </span>
                    </div>
                }

            </div>

            <GoDownBtn
                messages_wrapper_ref={messages_wrapper_ref}
                scrollDown={scrollDown}
            />

        </div>
    )
}

export default MessagesWrapper